.panel {
  background-color: white;
}

.panel__label {
  position: relative;
  display: block;
  width: 100%;
  background: none;
  border: none;
  text-align: left;
  padding: 25px 60px 25px 25px;
  font-weight: 700;
  font-size: 17px;
  font-family: "Nunito", sans-serif;
  transition: color 0.2s linear;
  cursor: pointer;
}

.panel__label:focus {
  outline: none;
}

.panel__label:after,
.panel__label:before {
  content: "";
  position: absolute;
  right: 25px;
  top: 50%;
  width: 22px;
  height: 2px;
  margin-top: -2px;
  background-color: #372717;
}

.panel__label:before {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  transition: -webkit-transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
  transition: transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
  transition: transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1), -webkit-transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
}

.panel[aria-expanded=true] .panel__content {
  opacity: 1;
}

.panel[aria-expanded=true] .panel__label {
  color: #ff7676;
}

.panel[aria-expanded=true] .panel__label:before {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.panel__inner {
  overflow: hidden;
  will-change: height;
  height: auto;
  transition: height 0.4s cubic-bezier(0.65, 0.05, 0.36, 1);
}

.panel__content {
  margin: 5px 25px 25px;
  font-size: 14px;
  height: auto;
  color: #756658;
  opacity: 0;
  transition: opacity 0.3s linear 0.18s;
}

.panel:not(:last-child) {
